import React , { useState } from 'react'
import { useMediaQuery } from 'react-responsive'
import {FaAlignRight , FaRegWindowClose , FaGithub, FaLinkedinIn, FaTwitter} from "react-icons/fa";
import './Navbar.css'


function Navbar() {
    const isMobile = useMediaQuery({ query: '(max-width: 1250px)' });
    const [toggle, setToggle] = useState(false);
    const [scroll, setScroll] = useState(false);
    
    const   togglemenu = () => {
            setToggle(!toggle);
        }

        window.addEventListener('scroll', () => {
            if (window.scrollY > 300)
                setScroll(true);
            else
                setScroll(false);
        });
    return (

        <div>
            <div className="nav" id={scroll ? "navscroll" : null}>
                <div className="logo">
                    <a href="#home" style={{ textDecoration: 'none' }}><h1>Keza<span>nn.</span></h1></a>
                </div>
                {
                    !isMobile 
                    ?
                        <div className="menu">
                            <ul className="lists">
                                <a href="#home" rel="webdevelopement" style={{ textDecoration: 'none'}}>Home</a>
                                <a  href="#about" rel="webdevelopement" style={{ textDecoration: 'none'}}>About</a>
                                <a href="#service" rel="webdevelopement" style={{ textDecoration: 'none'}}>What i do</a>
                                <a  href="#portfolio" rel="webdevelopement" style={{ textDecoration: 'none'}}>Work</a>
                                <button className="font-bold py-2 px-4 rounded-full" >
                                    <a href="mailto:akezannaya@gmail.com">Contact me</a>
                                </button>
                                <div className="social">
                                    <a href="https://github.com/akezann" rel="twitter"><FaGithub /></a>
                                    <a href="https://www.linkedin.com/in/kezann" target="_blank" rel="linkedin"><FaLinkedinIn /></a>
                                    <a href="github.com" rel="github"><FaTwitter /></a>
                                </div>
                               
                            </ul>

                        </div>
                    :   <div>
                            <span className="bar" onClick ={togglemenu}><FaAlignRight /></span>
                            {
                                toggle
                                ?
                                <div className={toggle ? 'menumobile' : 'hidden'} initial="out">
                                    <span className="toggle" onClick = {togglemenu}>< FaRegWindowClose/></span>
                                    <ul className="lists">
                                        <a href="#home" rel="webdevelopement" style={{ textDecoration: 'none'}} onClick={togglemenu}>Home</a>
                                        <a href="#about" rel="webdevelopement" style={{ textDecoration: 'none'}} onClick={togglemenu}>About</a>
                                        <a href="#service" rel="webdevelopement" style={{ textDecoration: 'none'}} onClick={togglemenu}>What i do</a>
                                        <a href="#portfolio" rel="webdevelopement" style={{ textDecoration: 'none'}} onClick={togglemenu}>Work</a>
                                        <button  onClick={togglemenu}>
                                            <a href="mailto:akezannaya@gmail.com">Contact me</a>
                                        </button>
                                        <div className="social">
                                            <a href="https://github.com/akezann" ><FaGithub /></a>
                                            <a href="hhttps://www.linkedin.com/in/kezann" target="_blank" rel="social" ><FaLinkedinIn /></a>
                                            <a href="github.com"><FaTwitter /></a>
                                        </div>
                                       
                                    </ul>

                                </div>
                                : null
                            }
                        </div>
                }
              
            </div>
        </div>
    )
}

export default Navbar;
